.backdrop-recipes {
  position: fixed;
  z-index: 2;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #05050548;
  overflow: auto;
  opacity: 1;
  visibility: visible;
  pointer-events: all;
  transition: opacity 250ms linear, visibility 250ms linear;
}

.close-modal {
  position: absolute;
  top: 14px;
  right: 14px;
  background-color: #00000000;
  border: none;
  width: 20px;
  height: 20px;
  display: flex;
  justify-content: center;
  padding: 0;
  align-items: center;
}
.close-modal-ico {
  stroke: #000000;
  fill: rgba(5, 5, 5, 0.5);
}
@media screen and (min-width: 768px) {
  .close-modal {
    top: 16px;
    right: 16px;
    width: 24px;
    height: 24px;
  }
  .close-modal-ico {
    width: 24px;
    height: 24px;
  }
}
.order-now-modal.is-hidden-modal {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transform: translate(-50%, -50%) scale(0.8);
  transition: opacity 250ms linear, visibility 250ms linear,
    transform 250ms linear;
}
.order-now-modal {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(1);
  padding: 28px 20px;
  width: 335px;
  height: 582px;
  background-color: var(--primary-white-color);
  border-radius: 15px;
  opacity: 1;
  visibility: visible;
  pointer-events: all;
  transition: opacity 250ms linear, visibility 250ms linear,
    transform 250ms linear;
}

.dark .order-now-modal {
  background-color: var(--primaty-black-color);
  color: var(--primary-white-color);
}

@media screen and (min-width: 768px) {
  .order-now-modal {
    width: 440px;
    height: 632px;
    padding: 40px;
  }
}

@media screen and (max-width: 1279px) and (orientation: landscape) {
  .order-now-modal {
    top: 90%;
  }
}

.order-now-title {
  display: block;
  font-size: 18px;
  line-height: 1.2;
  text-transform: uppercase;
  margin-bottom: 28px;
}

@media screen and (min-width: 768px) {
  .order-now-title {
    margin-bottom: 40px;
  }
}

.order-now-formfield {
  display: block;
  margin-bottom: 16px;
}

.order-now-formfield:last-of-type {
  margin-bottom: 40px;
}

@media screen and (min-width: 768px) {
  .order-now-formfield {
    margin-bottom: 18px;
  }
}

.order-now-name {
  display: block;
  color: rgba(5, 5, 5, 0.5);
  font-size: 14px;
  line-height: 1.2;
  font-weight: 500;
  margin-bottom: 8px;
}

.dark .order-now-name {
  font-weight: 400;
  color: rgba(255, 255, 255, 0.5);
}

.order-now-input,
.order-now-textarea {
  width: 100%;
  height: 46px;
  padding: 14px 18px;
  font-size: 14px;
  line-height: 1.2;
  border: 1px solid rgba(5, 5, 5, 0.2);
  border-radius: 15px;
  transition: border-color 250ms linear;
}

.dark .order-now-input,
.dark .order-now-textarea {
  background-color: inherit;
  color: var(--primary-white-color);
  border-color: rgba(255, 255, 255, 0.2);
}

@media screen and (min-width: 768px) {
  .order-now-input,
  .order-now-textarea {
    height: 48px;
  }
}

.order-now-input:focus,
.order-now-textarea:focus {
  outline: none;
  border-color: #dfdb22;
  transition: border-color 250ms linear;
}

.order-now-textarea {
  height: 100px;
  resize: none;
}

.send-order-btn {
  width: 100%;
  padding: 15px;
  background-color: var(--accent-color);
  color: var(--primary-white-color);
  font-size: 14px;
  line-height: 1.2;
  border: none;
  border-radius: 15px;
  transition: opacity 250ms linear;
}

.send-order-btn:disabled {
  opacity: 0.5;
  cursor: default;
}
