#pagination {
  height: 44px;

  display: none;

  margin: 0 0 100px 0;
}

#pagination .tui-page-btn {
  padding: 12px 0;
  width: 44px;
  height: 44px;
  border-radius: 50%;
}

#pagination.tui-pagination .tui-first-child.tui-is-selected {
  border-left: none;
}

#pagination.tui-pagination .tui-is-selected,
.tui-pagination strong {
  margin-right: 8px;
  background: var(--accent-color);
  border: var(--accent-color);
  color: var(--primaty-black-color);
}

#pagination.tui-pagination .tui-page-btn {
  font-size: 18px;
}

#pagination .more-button {
  padding: 7px 0;
}

#pagination .move-button {
  padding: 7px 0;

  background-color: var(--accent-color);
}

#pagination .prev-button {
  padding: 7px 0;

  background-color: var(--primaty-black-color);
}

#pagination.tui-pagination .tui-prev {
  margin-right: 24px;
}

#pagination.tui-pagination .tui-first.prev-button,
#pagination .tui-page-btn.tui-first.move-button {
  margin-right: 8px;
}

#pagination .pag-page {
  margin-right: 10px;
}

#pagination .tui-page-btn.tui-prev-is-ellip.more-button.tui-first-child {
  margin-right: 10px;
}

#pagination .tui-page-btn.tui-is-selected.tui-last-child,
#pagination .tui-page-btn.pag-page.tui-last-child {
  margin-right: 0;
}

#pagination .tui-page-btn.tui-next.move-button,
#pagination .tui-page-btn.tui-is-disabled.tui-next.prev-button {
  margin-left: 24px;
}

#pagination .tui-page-btn.tui-last.move-button,
#pagination .tui-page-btn.tui-is-disabled.tui-last.prev-button {
  margin-left: 8px;
}

@media screen and (min-width: 768px) and (max-width: 1279px) {
  #pagination {
    margin-left: -230px;
  }

  #favorites-container #pagination {
    margin-left: auto;
  }
}
@media screen and (max-width: 767px) {
  #pagination {
    height: 40px;
    margin-bottom: 80px;
  }

  #pagination .tui-page-btn {
    width: 40px;
    height: 40px;
  }

  #pagination.tui-pagination .tui-page-btn {
    font-size: 18px;
  }

  #pagination .more-button {
    padding: 5px 0;
  }

  #pagination .move-button {
    padding: 5px 0;
  }

  #pagination .prev-button {
    padding: 5px 0;
  }

  #pagination.tui-pagination .tui-prev {
    margin-right: 14px;
  }

  #pagination .tui-page-btn.tui-next.move-button,
  #pagination .tui-page-btn.tui-is-disabled.tui-next.prev-button {
    margin-left: 14px;
  }

  #pagination.tui-pagination .tui-first.prev-button,
  #pagination .tui-page-btn.tui-first.move-button {
    margin-right: 4px;
  }

  #pagination .tui-page-btn.tui-last.move-button,
  #pagination .tui-page-btn.tui-is-disabled.tui-last.prev-button {
    margin-left: 4px;
  }

  #pagination .pag-page {
    margin-right: 8px;
  }

  #pagination .tui-page-btn.pag-page:nth-child(n) {
    margin-left: 8px;
  }

  #pagination .tui-page-btn.pag-page:nth-child(2n + 1) {
    display: none;
  }

  #pagination .tui-page-btn.tui-next-is-ellip.more-button.tui-last-child {
    margin-left: 8px;
  }

  #pagination .tui-page-btn.tui-is-selected {
    margin-right: 0;
  }

  #pagination .tui-page-btn.tui-prev-is-ellip.more-button.tui-first-child {
    margin-right: 8px;
  }

  #pagination
    .tui-page-btn.tui-prev-is-ellip.more-button.tui-first-child
    + .tui-page-btn.tui-is-selected {
    margin-left: 8px;
  }

  #pagination
    .tui-page-btn.tui-is-selected.tui-first-child
    + .tui-page-btn.pag-page {
    margin-right: 0;
  }

  #pagination
    .tui-page-btn.tui-prev-is-ellip.more-button.tui-first-child
    + .tui-page-btn.pag-page {
    display: none;
  }

  #pagination
    .tui-page-btn.tui-prev-is-ellip.more-button.tui-first-child
    ~ .tui-page-btn.tui-is-selected {
    margin-left: 0;
  }
}

.pag-page-next-dark {
  color: var(--primary-white-color) !important;
  border-width: 1px !important;
  border-style: solid !important;
  border-color: rgba(255, 255, 255, 0.3) !important;
}

.pag-page-prev-dark {
  color: #ffffff80 !important;
  border-width: 1px !important;
  border-style: solid !important;
  border-color: var(--primaty-black-color) !important;
}

.pag-page-move-dark {
  color: var(--primaty-black-color) !important;
  border-width: 1px !important;
  border-style: solid !important;
  border-color: var(--accent-color) !important;
}

.pag-page-next-light {
  color: var(--primaty-black-color) !important;
  border-width: 1px !important;
  border-style: solid !important;
  border-color: rgba(5, 5, 5, 0.3) !important;
}

.pag-page-prev-light {
  color: #f8f8f880 !important;
  border-width: 1px !important;
  border-style: solid !important;
  border-color: var(--primaty-black-color) !important;
}

.pag-page-move-light {
  color: var(--primaty-black-color) !important;
  border-width: 1px !important;
  border-style: solid !important;
  border-color: var(--accent-color) !important;
}

.pag-btns {
  color: inherit !important;
  border-width: 1px !important;
  border-color: inherit !important;
}

.pag-active-dark {
  color: var(--primary-white-color) !important;
}
.pag-active-light {
  color: var(--primaty-black-color) !important;
}
