@font-face {
  font-family: 'Inter';
  src: url('../../fonts/Inter-Regular.woff2'),
    url('../../fonts/Inter-Regular.woff');
  font-weight: normal;
}

@font-face {
  font-family: 'Inter';
  src: url('../../fonts/Inter-Medium.woff2'),
    url('../../fonts/Inter-Medium.woff');
  font-weight: 500;
}

@font-face {
  font-family: 'Inter';
  src: url('../../fonts/Inter-SemiBold.woff2'),
    url('../../fonts/Inter-SemiBold.woff');
  font-weight: 600;
}

:root {
  /* White Theme */
  --primary-white-color: #ffffff;
  --primaty-black-color: #050505;
  --text-white-color: #f8f8f8;
  --accent-color: #9bb537;
  /* Dark Theme */
  --primary-dark-color: #1e1e1e;
}

body{
  font-family: 'Inter';
color: rgba(5, 5, 5, 0.50);
}
* {
  box-sizing: border-box;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}

svg {
  fill: currentColor;
  stroke: currentColor;
}

.container {
  margin: 0 auto;
}

@media screen and (max-width: 767px) {
  .container {
    min-width: 375px;

    padding: 0 20px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;

    padding: 0 32px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    width: 1080px;

    padding: 0 15px;
  }
}

.position-wrapper {
  display: flex;
  flex-direction: column;
}

@media screen and (min-width: 768px) {
  .position-wrapper {
    flex-wrap: wrap;
    max-height: 1423px;

    align-items: center;
    column-gap: 32px;
  }
}

@media screen and (min-width: 1280px) {
  .position-wrapper {
    column-gap: 64px;
    max-height: 1244px;
  }
}

